            <table>
              <div>
                <table>
                  <tr>
                    <div style="text-align: left"><strong>本機能の注意事項</strong></div>
                    <div style="text-align: left">
                      <ul>
                        <li style="color: red">30-DAY成長補給品は全てコアダストに変換するものとして計算</li>
                        <li>基地防御報酬の数値はゲーム内と僅かに違いあり</li>
                        <li>資源バランスを考慮した課金の最適化はしていないため、計算結果の課金額は参考程度とする事</li>
                        <li>ウィークリーやマンスリー商品の課金計上は次の火曜ないし1日を迎えたところで行うため、週や月を跨がずにレベルアップする場合は課金額が0になる場合がある</li>
                      </ul>
                    </div>
                    <p/>
                    <div style="text-align: left"><strong>計算に含まれるもの（最大報酬で計算）</strong></div>
                    <div style="text-align: left">
                      <ul>
                        <li>アカデミー効果、シミュレーションクリア報酬、鉄くずショップ</li>
                        <li>デイリーミッション報酬、ウィークリーミッション報酬</li>
                      </ul>
                    </div>
                    <p/>
                    <div style="text-align: left"><strong>計算に含まれないもの（一例）</strong></div>
                    <div style="text-align: left">
                      <ul>
                        <li>イベント報酬、フルバーストデイ報酬</li>
                      </ul>
                    </div>
                  </tr>
                </table>
              </div>
            </table>
          </td>
        </table>
