      fsSgHitRate: 70,  // SGヒット率(%)
      fsAtkToHpRetioAttack: 23.49,
      fsAtkToHpRetioSupport: 31.18,
      fsAtkToHpRetioDefence: 42.74,
      fsShowSetting: 0, // アタッカー詳細設定欄表示(0:表示なし、1:詳細表示、2:簡易表示)
      fsShowUseSkill: 0, // 使用スキル欄表示(0:表示なし、1:簡易表示、2:詳細表示)
      fsShowFormula: false, // 計算式欄表示(true:表示、false:省略)

      fsTouchDamageId: null, // 前回タッチした行（タッチデバイス用）
      fsTouchAttackerName: null, // 前回タッチしたアタッカー（タッチデバイス用）
      fsTouchSupporterName: null, // 前回タッチしたサポーター（タッチデバイス用）

      fsLevelProt: {
          value: "" // スキル効果値
      },
      fsUseSkillProt: {
          id: 0
        , note: "" // スキル説明
        , titleFlg: false // タイトルフラグ(false:非タイトル、true:タイトル)
        , skillCls: null // スキル区分(1:スキル1、2:スキル2、3:バースト)
        , skillTarget: "" // スキル対象
        , effect: "" // 種別
        , effectAdd: "" // 追加効果
        , lvs: [] // レベルごとの値
        , calcSkillLv: null // スキルレベル(計算時専用)
        , stackMax: 1 // 最大スタック
        , stackVal: 1 // 指定スタック(計算時に利用)
        , condAttack: "" // スキル発動条件：攻撃方法
        , condAttackTarget: "" // スキル発動条件：攻撃対象
        , condAllyWeapon: "" // スキル発動条件：味方武器
        , condAllyElement: "" // スキル発動条件：味方属性
        , condAllyClass: "" // スキル発動条件：味方型
        , condEnemyClass: "" // スキル発動条件：敵属性
        , applyBurst: "" // 適用補正：フルバースト
        , applyCharge: "" // 適用補正：チャージダメージ
        , useSkillFlg: "" // 使用スキル
        , useSkillCheck: false // 使用スキルチェック状態
        , useSkillInvalid: false // 使用スキル無効状態
      }, // 使用スキルひな形
      fsSupporterProt: { 
          id: 0
        , order: 1
        , supporterName: ''
        , supporterSkills: [] // サポーターのスキル
        , supporterSkillLv: [0, 10, 10, 10]
      }, // サポーター情報ひな形
      fsDamageListProt: {
          id: 0 // 配列の要素数と合わせるため0スタート
        , disp: true
        , order: 1
        , attackerName: ''
        , attackerSkills: [] // アタッカーのスキル
        , attackerSkillLv: [0, 10, 10, 10]
        , attackList: [] // 攻撃方法選択肢
        , attackCls: '通常攻撃' // 選択している攻撃方法
        , prmCoreHit: false // コアヒット
        , prmFullBurst: false // フルバースト
        , prmCritical: false // クリティカル
        , prmCompElement: false // 相性有利
        , prmCampFar: false // 適性距離
        , prmAttack: 100000 // 攻撃力
        , prmEnemyDef: 13125 // 敵防御力
        , prmAttackDmgUp: [0, 0, 0, 0] // 攻撃力up(%)
        , prmElementDmgUp: [0, 0, 0, 0, 0] // 有利コードup(%) オバロ4個所＋キューブ効果
        , prmCriticalDmgUp: [0, 0, 0, 0] // クリダメup(%)
        , prmChargeDmgUp: [0, 0, 0, 0] // チャージダメージup(%)
        , supporters: [] // サポーター情報
        , calcDamage: 0
        , calcDamageFormula: ""
      }, // 一覧データひな形
      damageListMaxNum: 10, // 一覧データ最大数
      damageListNum: 0, // 一覧データ現在数
      damageList: [],// 一覧データ

      coOvlAttackDmgUp:   [0, 4.77, 5.47,  6.18,  6.88,  7.59,  8.29,  9.00,  9.70,  10.40, 11.11, 11.81, 12.52, 13.22, 13.93, 14.63],
      coOvlElementDmgUp:  [0, 9.54, 10.94, 12.34, 13.75, 15.15, 16.55, 17.95, 19.35, 20.75, 22.15, 23.56, 24.96, 26.36, 27.76, 29.16],
      coOvlCriticalDmgUp: [0, 6.64, 7.62,  8.60,  9.58,  10.56, 11.54, 12.52, 13.50, 14.48, 15.46, 16.44, 17.42, 18.40, 19.38, 20.36],
      coOvlChargeDmgUp:   [0, 4.77, 5.47,  6.18,  6.88,  7.59,  8.29,  9.00,  9.70,  10.40, 11.11, 11.81, 12.52, 13.22, 13.93, 14.63],
      coCubeElementDmgUp: [0, 8.48, 10.60, 12.72],
    }
  },
  
  mounted(){
    let _this = this; // forEach内のthisは配列を指してしまうのでthisを別名にしておく

    // 火力シミュの一覧データを規定数までコピー
    for (let i = 0; i <= _this.damageListMaxNum - 1; ++i) {
      let newDamage = JSON.parse(JSON.stringify(_this.fsDamageListProt));
      newDamage.id = i;
      newDamage.disp = (i == 0) ? true : false;
      newDamage.order = i + 1;
      for (let i = 0; i <= 3; ++i) {
        let newSupporter = JSON.parse(JSON.stringify(_this.fsSupporterProt));
        newSupporter.id = i;
        newSupporter.order = i + 1;
        newDamage.supporters.push(newSupporter);
      }
      _this.damageList.push(newDamage);
    }

    let dragX;
    let dragY;
    let smZIndexCnt = 1;
    let dragPre = null;
    let scrollTop;
    let scrollLeft;
    let startTime = performance.now();

    function mdown(e) {
      //クラス名に .dragTag を追加
      this.classList.add("dragTag");
      // タッチデバイスとマウスのイベントの差異を吸収
      if(e.type === "mousedown") var event = e;
      else var event = e.changedTouches[0];
      // 要素内の相対座標を取得
      dragX = event.pageX - this.offsetLeft;
      dragY = event.pageY - this.offsetTop;
      // ムーブイベントにコールバック
      document.body.addEventListener("mousemove", mmove, false);
      document.body.addEventListener("touchmove", mmove, false);
      // 所属している要素のスクロール量を取得
      let parentElement = document.getElementById(this.parentNode.id); 
      scrollTop  = parentElement.scrollTop;
      scrollLeft = parentElement.scrollLeft;

      if (e.type === "touchstart") {
        // ダブルタップ判定
        let drag = document.getElementsByClassName("dragTag")[0];
        if (drag == dragPre) { // 前回タップした要素と同じ要素の場合はダブルタップとみなす
          if (drag.parentNode.id.startsWith('squad')) {
            mdblclick(e);
            _this.mCoNikkeDbl(drag.id, 'squad', 'sfCandidate'); // 部隊編成でのダブルタップイベント
          } else {
            mdblclick(e);
            _this.mCoNikkeDbl(drag.id, 'smGraph', 'smCandidate'); // 散布図でのダブルタップイベント
          }
        } else {
          dragPre = drag; // タップした要素を記録
          setTimeout(function () {dragPre = null;}, 500); // 500ms以内にタップされなければタップ記録をクリア
        }

        //タッチデバイスフラグON
        _this.coTouchDevice = true;
      }

      startTime = performance.now();
    }

    function mdownFsSetting(e) {
      //クラス名に .fireTouch を追加
      this.classList.add("TouchSetting");

      if (e.type === "touchstart") {
        // ダブルタップ判定
        let drag = document.getElementsByClassName("TouchSetting")[0];
        if (drag == dragPre) { // 前回タップした要素と同じ要素の場合はダブルタップとみなす
          _this.fsShowSetting++;
          if(_this.fsShowSetting >= 3) _this.fsShowSetting = 1;
          drag.classList.remove("TouchSetting"); // クラス名を消す
        } else {
          dragPre = drag; // タップした要素を記録
          setTimeout(function () {dragPre = null;}, 500); // 500ms以内にタップされなければタップ記録をクリア
        }
        //タッチデバイスフラグON
        _this.coTouchDevice = true;
      }
    }

    function mdownFsUseSkill(e) {
      //クラス名に .fireTouch を追加
      this.classList.add("TouchUseSkill");

      if (e.type === "touchstart") {
        // ダブルタップ判定
        let drag = document.getElementsByClassName("TouchUseSkill")[0];
        if (drag == dragPre) { // 前回タップした要素と同じ要素の場合はダブルタップとみなす
          _this.fsShowUseSkill++;
          if (_this.fsShowUseSkill == 3) _this.fsShowUseSkill = 0;
          drag.classList.remove("TouchUseSkill"); // クラス名を消す
        } else {
          dragPre = drag; // タップした要素を記録
          setTimeout(function () {dragPre = null;}, 500); // 500ms以内にタップされなければタップ記録をクリア
        }
        //タッチデバイスフラグON
        _this.coTouchDevice = true;
      }
    }

    function mdownFsFormula(e) {
      //クラス名に .fireTouch を追加
      this.classList.add("TouchFormula");

      if (e.type === "touchstart") {
        // ダブルタップ判定
        let drag = document.getElementsByClassName("TouchFormula")[0];
        if (drag == dragPre) { // 前回タップした要素と同じ要素の場合はダブルタップとみなす
          _this.fsShowFormula = !_this.fsShowFormula;
          drag.classList.remove("TouchFormula"); // クラス名を消す
        } else {
          dragPre = drag; // タップした要素を記録
          setTimeout(function () {dragPre = null;}, 500); // 500ms以内にタップされなければタップ記録をクリア
        }
        //タッチデバイスフラグON
        _this.coTouchDevice = true;
      }
    }

    function mdblclick(e) {
      // ドラッグしている要素を取得
      let drag = document.getElementsByClassName("dragTag")[0];
      // ムーブベントハンドラの消去
      document.body.removeEventListener("mousemove", mmove, false);
      document.body.removeEventListener("touchmove", mmove, false);
      drag.removeEventListener("mouseup", mup, false);
      drag.removeEventListener("touchend", mup, false);
      document.body.removeEventListener("mouseleave", mup, false);
      document.body.removeEventListener("touchleave", mup, false);
      // クラス名 .dragTag も消す
      while (document.getElementsByClassName("dragTag")[0] != undefined) {
        let element = document.getElementsByClassName("dragTag")[0];
        element.classList.remove("dragTag");
      };
      //drag.style.position = "relative";
      //drag.style.top = "0px";
      //drag.style.left = "0px";
      //drag.style.zIndex = 1;
    }

    function mmove(e) {
      // フリックしたときに画面を動かさないようにデフォルト動作を抑制
      e.preventDefault();
      // ドラッグしている要素を取得
      let drag = document.getElementsByClassName("dragTag")[0];
      // 同様にマウスとタッチの差異を吸収
      if(e.type === "mousemove") var event = e;
      else var event = e.changedTouches[0];
      // 所属している要素のスクロール量を取得
      //let parentElement = document.getElementById(drag.parentNode.id); 
      //scrollTop  = parentElement.scrollTop;
      //scrollLeft = parentElement.scrollLeft;
      // マウスが動いた場所に要素を動かす
      drag.style.position = "absolute";
      drag.style.top = event.pageY - scrollTop - dragY + "px";
      drag.style.left = event.pageX - scrollLeft - dragX + "px";
      drag.style.zIndex = 9999;
      // マウスボタンが離されたとき、またはカーソルが外れたとき発火
      drag.addEventListener("mouseup", mup, false);
      drag.addEventListener("touchend", mup, false);
      document.body.addEventListener("mouseleave", mup, false);
      document.body.addEventListener("touchleave", mup, false);
    }

    function mup(e) {
      let drag = document.getElementsByClassName("dragTag")[0];
      // ムーブベントハンドラの消去
      document.body.removeEventListener("mousemove", mmove, false);
      document.body.removeEventListener("touchmove", mmove, false);
      drag.removeEventListener("mouseup", mup, false);
      drag.removeEventListener("touchend", mup, false);
      document.body.removeEventListener("mouseleave", mup, false);
      document.body.removeEventListener("touchleave", mup, false); 
      // クラス名 .dragTag も消す
      while (document.getElementsByClassName("dragTag")[0] != undefined) {
        let element = document.getElementsByClassName("dragTag")[0];
        element.classList.remove("dragTag");
      };

      //タッチデバイスとマウスのイベントの差異を吸収
      drag.style.display = "none";
      if (e.type == "mouseup" || e.type == "mouseleave") {
        var x = e.clientX;
        var y = e.clientY;
      } else {
        let t = e.changedTouches[0];
        var x = t.clientX;
        var y = t.clientY;
      }
      let dropPoint = document.elementFromPoint(x, y);
      drag.style.display = "";

      //ドロップ先にある要素による違いを吸収
      if (dropPoint.id.includes("fsImgAttacker")) {
        var drop = dropPoint.parentNode.parentNode.parentNode;
      } else if (dropPoint.id.includes("fsImgSupporter")) {
        var drop = dropPoint.parentNode.parentNode.parentNode.parentNode;
      } else if (dropPoint.id.includes("fsMarkBurstAtk")) {
        var drop = dropPoint.parentNode.parentNode.parentNode.parentNode;
      } else if (dropPoint.id.includes("fsMarkBurstSup")) {
        var drop = dropPoint.parentNode.parentNode.parentNode.parentNode.parentNode;
      } else if (dropPoint.id.includes("fsMsgAttacker")) {
        var drop = dropPoint.parentNode;
      } else if (dropPoint.id.includes("fsMsgSupporter")) {
        var drop = dropPoint.parentNode;
      } else if (dropPoint.id.includes("nikke")) {
        var drop = dropPoint.parentNode;
      } else if (dropPoint.id.includes("mark")) {
        var drop = dropPoint.parentNode.parentNode;
      } else {
        var drop = dropPoint;
      }

      if (drop.id.includes("squad")) { // ドロップ先が部隊1～6
        let idxTo = drop.childNodes.length; // ドロップ先の要素番号（デフォルトは最大値＝末尾）
        let idxFrom = 0; // ドロップ元の要素番号
        let cnt = 0;
        // ドロップ先の画像が部隊の枠の中の何番目の要素なのか探す
        drop.childNodes.forEach(function(item){
          if(item.id == dropPoint.id) idxTo = cnt;
          cnt++;
        });
        // ドロップ元と先が同一部隊内の場合、ドロップ元の要素番号を探す
        cnt = 0;
        if(drop.id == drag.parentNode.id) {
          drop.childNodes.forEach(function(item){
            if(item.id == drag.id) idxFrom = cnt;
            cnt++;
          });
          if(idxFrom < idxTo) { // ドロップ元要素番号<ドロップ先要素番号の場合、挿入先をドロップ先要素の後ろにする
            idxTo++;
          }
          if(performance.now() - startTime < 200) { //ドラック開始～終了が早すぎる場合は元の場所へ移動（ダブルクリックを考慮）
            idxTo = idxFrom;
          }
        }
        
        drop.insertBefore(drag, drop.childNodes.item(idxTo)); // ドロップ先画像を挿入
        drag.style.position = "relative";
        drag.style.top = "0px";
        drag.style.left = "0px";
        drag.style.zIndex = 1;

        _this.mSaveQf(_this.sqSquadSelect);
      } else if (drop.id.includes("Candidate")) { // ドロップ先が"候補一覧"
        let idx = 0;
        let cnt = 0;
        drop.childNodes.forEach(function(item){
          if(item.id != undefined && item.id.includes('nikke')) { // 散布図の場合は上下左右のコメントがあるのでnikke画像に限定
            if(item.id <= drag.id) idx = cnt+1;
            cnt++;
          }
        });
        drop.insertBefore(drag, drop.childNodes.item(idx));
        drag.style.position = "relative";
        drag.style.top = "0px";
        drag.style.left = "0px";
        drag.style.zIndex = 1;

        if (drop.id == "sfCandidate") _this.mSaveQf(_this.sqSquadSelect);

      } else if (drop.id == "smGraph") { // ドロップ先が"散布図グラフ"
        let dropRect = document.getElementById(drop.id).getBoundingClientRect(); // グラフの座標
        let dragRect = document.getElementById(drag.id).getBoundingClientRect(); // ニケ画像の座標
        drop.appendChild(drag);
        drag.style.position = "absolute";
        drag.style.top = y - dropRect.top - dragRect.height/2 + "px";
        drag.style.left = x - dropRect.left - dragRect.width/2 + "px";
        drag.style.zIndex = ++smZIndexCnt;

      } else if (drop.id.includes("fsAttacker")) { // ドロップ先が火力シミュのアタッカー
        //ドラック中の画像を元の位置に戻す
        drag.style.position = "relative";
        drag.style.top = "0px";
        drag.style.left = "0px";
        drag.style.zIndex = 1;

        //ドラック先の一覧データにアタッカー名を設定
        let damageIdx = Number(drop.id.replace("fsAttacker", ""));
        _this.damageList.forEach(function(damage){
          if(damage.id == damageIdx) {
            if(damage.attackerName != drag.lang){
              damage.attackerName = drag.lang;
              _this.mFsChangeAttacker(damage);
              _this.mFsCalcDamage(damage);
            };
          };
        });


      } else if (drop.id.includes("fsSupporter")) { // ドロップ先が火力シミュのサポーター
        let damageIdx = Number(drop.id.replace("fsSupporter", ""));

        //ドラック中の画像を元の位置に戻す
        drag.style.position = "relative";
        drag.style.top = "0px";
        drag.style.left = "0px";
        drag.style.zIndex = 1;

        let setFlg = false;
        let orderCnt = 1;
        _this.damageList.forEach(function(damage){
          if(damage.id == damageIdx) {
            damage.supporters.forEach(function(supporter) {
              if (supporter.supporterName == drag.lang) {
                setFlg = true; // 既に同じ名前のサポーターが設定済みなら追加設定なしで終了させる
              } else if (setFlg == false && supporter.supporterName == '') {
                supporter.order = orderCnt;
                supporter.supporterName = drag.lang;
                setFlg = true;
                _this.mFsChangeSupporter(supporter);
                _this.mFsCalcDamage(_this.damageList[damageIdx]);
              }
              orderCnt++;
            });
          };
        });

        // 表示順でソート
        _this.damageList[damageIdx].supporters.sort(function(first, second){
          if (first.order > second.order){
            return 1;
          }else if (first.order < second.order){
            return -1;
          }else{
            return 0;
          }
        });

        // 表示順を1からの整数で付けなおし
        let cnt = 1;
        _this.damageList[damageIdx].supporters.forEach(function(supporter) {
          supporter.order = cnt;
          cnt++;
        });

      } else { // ドロップ先がそれ以外
        drag.style.position = "relative";
        drag.style.top = "0px";
        drag.style.left = "0px";
        drag.style.zIndex = 1;

      }
    }

    //ソートの矢印が表示されるよう起動時にNoを昇順ソート
    this.mSortBy('id', 'check');

    const asyncFunc = async () => { // 外部データ読み込み
      //const startTime = performance.now();
      let divisionsURL  = "https://raw.githubusercontent.com/nikke78f/NikkeTools/main/json/divisions.json";
      let contentsURL   = "https://raw.githubusercontent.com/nikke78f/NikkeTools/main/json/contents.json";
      let rewardURL     = "https://raw.githubusercontent.com/nikke78f/NikkeTools/main/json/reward.json";
      let experienceURL = "https://raw.githubusercontent.com/nikke78f/NikkeTools/main/json/experience.json";
      let packsURL      = "https://raw.githubusercontent.com/nikke78f/NikkeTools/main/json/packs.json";
      let nikkesURL     = "https://raw.githubusercontent.com/nikke78f/NikkeTools/main/json/nikkes.json";
      let updatesURL    = "https://raw.githubusercontent.com/nikke78f/NikkeTools/main/json/updates.json";
      await axios.get(divisionsURL).then(function(response) {_this.divisions = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(contentsURL).then(function(response) {_this.contents = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(rewardURL).then(function(response) {_this.reward = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(experienceURL).then(function(response) {_this.experience = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(packsURL).then(function(response) {_this.packs = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(nikkesURL).then(function(response) {_this.nikkes = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      await axios.get(updatesURL).then(function(response) {_this.updates = response.data;}.bind(_this)).catch(function(e) {console.error(e)});
      //const endTime = performance.now();

      //ニケ一覧を加工
      _this.nikkes.forEach(function(item){
          item.skill1Infos.forEach(function(info){
            item.skill1 += "\n" + info.note.replace("{%1}", info.lvs[9].value); // スキル効果内の置換対象をLv10の値に置き換え
          });
          item.skill2Infos.forEach(function(info){
            item.skill2 += "\n" + info.note.replace("{%1}", info.lvs[9].value);
          });
          item.burstInfos.forEach(function(info){
