  methods: {
    mFsShowSetting() { // 火力シミュ：使用スキル表示切替
      ++this.fsShowSetting;
      if (this.fsShowSetting >= 3) this.fsShowSetting = 1;
    },

    mFsShowUseSkill() { // 火力シミュ：使用スキル表示切替
      ++this.fsShowUseSkill;
      if (this.fsShowUseSkill == 3) this.fsShowUseSkill = 0;
    },

    mFsDblClickAtkTouch(damage) { // 火力シミュ：アタッカーダブルクリック(削除)
      let _this = this;

      // ダブルタップ判定
      // 前回タップした要素と同じ要素の場合はダブルタップとみなす
      if (_this.fsTouchDamageId == damage.id && _this.fsTouchAttackerName == damage.attackerName) {
        _this.mFsDblClickAtk(damage.id);
      } else {
        _this.fsTouchDamageId = damage.id;
        _this.fsTouchAttackerName = damage.attackerName;
        setTimeout(function(){
          _this.fsTouchDamageId = null;
          _this.fsTouchAttackerName = null;
        }, 500); // 500ms以内にタップされなければタップ記録をクリア
      }
    },

    mFsDblClickAtk(damageId) { // 火力シミュ：アタッカーダブルクリック(削除)
      this.damageList.forEach(function(damage){
        if(damage.id == damageId) {
          damage.attackerName = "";
          damage.calcDamage = 0;
          damage.calcDamageFormula = "";
        };
      });
    },

    mFsDblClickSupTouch(damage, supporter) { // 火力シミュ：サポーターダブルクリック(削除)
      let _this = this;

      // ダブルタップ判定
      // 前回タップした要素と同じ要素の場合はダブルタップとみなす
      if (_this.fsTouchDamageId == damage.id && _this.fsTouchSupporterName == supporter.name) {
        _this.mFsDblClickSup(damage, supporter);
      } else {
        _this.fsTouchDamageId = damage.id;
        _this.fsTouchSupporterName = supporter.name;
        setTimeout(function(){
          _this.fsTouchDamageId = null;
          _this.fsTouchSupporterName = null;
        }, 500); // 500ms以内にタップされなければタップ記録をクリア
      }
    },

    mFsDblClickSup(damage, supporter) { // 火力シミュ：サポーターダブルクリック(削除)
      supporter.order = 999; // ソートのため大きな数字を一旦設定
      supporter.supporterName = '';

      // 表示順でソート
      damage.supporters.sort(function(first, second){
        if (first.order > second.order){
          return 1;
        }else if (first.order < second.order){
          return -1;
        }else{
          return 0;
        }
      });

      // 表示順を1からの整数で付けなおし
      let cnt = 1;
      damage.supporters.forEach(function(supporter) {
        supporter.order = cnt;
        cnt++;
      });
      
      this.mFsCalcDamage(damage);
    },

