            newSkill.effectAdd = info.effectAdd;
            info.lvs.forEach(function(lv){
              newLv = JSON.parse(JSON.stringify(_this.fsLevelProt));
              newLv.value = lv.value;
              newSkill.lvs.push(newLv);
            });
            newSkill.stackMax = (info.stack == "") ? 1 : Number(info.stack);
            newSkill.stackVal = newSkill.stackMax;
            newSkill.condAttack = info.condAttack;
            newSkill.condAttackTarget = info.condAttackTarget;
            newSkill.condAllyWeapon = info.condAllyWeapon;
            newSkill.condAllyElement = info.condAllyElement;
            newSkill.condAllyClass = info.condAllyClass;
            newSkill.condEnemyClass = info.condEnemyClass;
            newSkill.applyBurst = info.applyBurst;
            newSkill.applyCharge = info.applyCharge;
            if(info.useSkillFlg == "1" && (info.skillTarget == '味方' || (info.skillTarget == '敵' && info.effect == "防御力"))){
              newSkill.useSkillFlg = 1;
            } else {
              newSkill.useSkillFlg = 0;
            }
            if(newSkill.useSkillFlg == 1){
              newSkill.useSkillCheck = true;
            } else {
              newSkill.useSkillCheck = false;
            }
            newSkill.useSkillInvalid = false;
            supporter.supporterSkills.push(newSkill);
          });
          
          newSkill = JSON.parse(JSON.stringify(_this.fsUseSkillProt));
          newSkill.id = supporter.supporterSkills.length;
          newSkill.note = "バースト：" + nikke.burstCt;
          newSkill.titleFlg = true;
          newSkill.skillCls = 3;
          for (let i = 0; i < 10; ++i) {
            newLv = JSON.parse(JSON.stringify(_this.fsLevelProt));
            newLv.value = "";
            newSkill.lvs.push(newLv);
          };
          newSkill.useSkillFlg = 0;
          supporter.supporterSkills.push(newSkill);

          nikke.burstInfos.forEach(function(info){
            newSkill = JSON.parse(JSON.stringify(_this.fsUseSkillProt));
            newSkill.id = supporter.supporterSkills.length;
            newSkill.note = info.note;
            newSkill.titleFlg = false;
            newSkill.skillCls = 3;
            newSkill.skillTarget = info.skillTarget;
            newSkill.effect = info.effect;
            newSkill.effectAdd = info.effectAdd;
            info.lvs.forEach(function(lv){
              newLv = JSON.parse(JSON.stringify(_this.fsLevelProt));
              newLv.value = lv.value;
              newSkill.lvs.push(newLv);
            });
            newSkill.stackMax = (info.stack == "") ? 1 : Number(info.stack);
            newSkill.stackVal = newSkill.stackMax;
            newSkill.condAttack = info.condAttack;
            newSkill.condAttackTarget = info.condAttackTarget;
            newSkill.condAllyWeapon = info.condAllyWeapon;
            newSkill.condAllyElement = info.condAllyElement;
            newSkill.condAllyClass = info.condAllyClass;
            newSkill.condEnemyClass = info.condEnemyClass;
            newSkill.applyBurst = info.applyBurst;
            newSkill.applyCharge = info.applyCharge;
            if(info.useSkillFlg == "1" && (info.skillTarget == '味方' || (info.skillTarget == '敵' && info.effect == "防御力"))){
              newSkill.useSkillFlg = 1;
            } else {
              newSkill.useSkillFlg = 0;
            }
            if(newSkill.useSkillFlg == 1){
              newSkill.useSkillCheck = true;
            } else {
              newSkill.useSkillCheck = false;
            }
            newSkill.useSkillInvalid = false;
            supporter.supporterSkills.push(newSkill);
          });
        }
      });
    },

    mFsCalcDamageAll() { // 火力シミュ：ダメージ計算(全て)
      let _this = this;
      _this.damageList.forEach(function(damage){
        if (damage.attackerName != '') _this.mFsCalcDamage(damage);
      });
    },

    mFsCalcDamage(damage) { // 火力シミュ：ダメージ計算
      let _this = this;
      let flgNormalAttack = false;

      // アタッカー情報をニケ一覧から取り出し
      let attacker = _this.nikkes.find(e => e.name === damage.attackerName);
      if (attacker == undefined) return;

      // スキルレベルを計算時専用変数へ反映
      damage.attackerSkills.forEach(function(skill){
        if(skill.skillCls == 1)      skill.calcSkillLv = damage.attackerSkillLv[1] - 1;
        else if(skill.skillCls == 2) skill.calcSkillLv = damage.attackerSkillLv[2] - 1;
        else                         skill.calcSkillLv = damage.attackerSkillLv[3] - 1;
      });
      damage.supporters.forEach(function(supporter){
        supporter.supporterSkills.forEach(function(skill){
          if(skill.skillCls == 1)      skill.calcSkillLv = supporter.supporterSkillLv[1] - 1;
          else if(skill.skillCls == 2) skill.calcSkillLv = supporter.supporterSkillLv[2] - 1;
          else                         skill.calcSkillLv = supporter.supporterSkillLv[3] - 1;
        });
      });

      // 無効状態設定
      damage.attackerSkills.forEach(function(skill){
        if(skill.useSkillCheck) skill.useSkillInvalid = true; // 使用チェックOnなら一旦無効表示にし、このあと計算に使うときに無効を解除する
        else skill.useSkillInvalid = false; // 使用チェックOffは無効表示を解除
      });
      damage.supporters.forEach(function(supporter){
        supporter.supporterSkills.forEach(function(skill){
          if(skill.useSkillCheck) skill.useSkillInvalid = true; // 使用チェックOnなら一旦無効表示にし、このあと計算に使うときに無効を解除する
          else skill.useSkillInvalid = false; // 使用チェックOffは無効表示を解除
        });
      });

      // 結果の初期化
      damage.calcDamage = 0;
      damage.calcDamageFormula = "";

      //　アタッカーのスキル説明の行数だけループ
      damage.attackerSkills.forEach(function(skill){
        let flgCalc = false; // 計算対象フラグ
        let flgIgnoreDef = false; // 防御無視フラグ
        let flgAddDamage = false; // 追加ダメージフラグ

        let note = "" // 攻撃方法・スキル説明
        let attackTarget = "" // 攻撃対象
        let atkBase = damage.prmAttack; // 基本攻撃力
        let atkBuf = 0; // 攻撃バフスキル倍率(%)
        let atkAdd = 0; // 攻撃バフスキル(加算)
        let atkBufOvl = Math.round((damage.prmAttackDmgUp[0] + damage.prmAttackDmgUp[1] + damage.prmAttackDmgUp[2] + damage.prmAttackDmgUp[3])*100)/100; // オバロ攻撃力op倍率(%)
        let defEne = damage.prmEnemyDef; // 敵防御力
        let defDbf = 0; // 防御力デバフスキル倍率(%)
        let opCri = damage.prmCritical ? 0.5 : 0; // クリティカル(0 or 0.5)
        let opCriBuf = damage.prmCritical ? Math.round((damage.prmCriticalDmgUp[0] + damage.prmCriticalDmgUp[1] + damage.prmCriticalDmgUp[2] + damage.prmCriticalDmgUp[3])*100)/100 : 0; // クリダメupスキル倍率(%)
        let opCore = damage.prmCoreHit ? (attacker.coreRate - 100) / 100 : 0; // コアヒット(0 or 1)
        let opRenge = damage.prmCampFar ? 0.3 : 0; // 距離ボーナス(0 or 0.3)
        let opBurst = damage.prmFullBurst ? 0.5 : 0; // フルバースト(0 or 0.5)
        let opEle = damage.prmCompElement ? 0.1 : 0; // 属性有利(0 or 0.1)
        let opEleCode = Math.round((damage.prmElementDmgUp[0] + damage.prmElementDmgUp[1] + damage.prmElementDmgUp[2] + damage.prmElementDmgUp[3] + damage.prmElementDmgUp[4])*100)/100; // オバロ有利コード + キューブ有利コード
        let opCharge = (attacker.chargeScale == 0) ? 100 : attacker.chargeScale + Math.round((damage.prmChargeDmgUp[0] + damage.prmChargeDmgUp[1] + damage.prmChargeDmgUp[2] + damage.prmChargeDmgUp[3])*100)/100; // チャージ(1～)
        let opChargeComp = 0; // 強制チャージ(1～)
        let opParts = 0; // 部位ダメージスキル倍率(%)
        let opOther = 0; // 他のダメージアップスキル倍率(%) ex.部位ダメージ
        let opPlus = 0; // 簡易ダメージスキル倍率(%) ex.受けるダメージ
        let atkWep = 0; // 武器倍率(%)、又は、直接ダメージスキル倍率(%)
        let atkWepDefIgn = 0; // 防御無視ダメージスキル倍率(%)
        let prmMaxHp = 0; // 最大HP
        let maxHpBuf = 0; // 最大HPバフスキル倍率(%)

        if (skill.condAttack == damage.attackCls || skill.condAttack == "") {
          // 画面で指定した攻撃方法が通常攻撃の場合、1ループ目は通常攻撃の計算を行う
          if (damage.attackCls == "通常攻撃") {
            if (!flgNormalAttack) {
              note = "■通常攻撃"
              atkWep += Number(attacker.weaponRate);
              flgNormalAttack = true;
              flgCalc = true;
            }
          }

          if (skill.effect == "ダメージ(最終攻撃力)" && (skill.condAttack == damage.attackCls || skill.condAttack == "")) {
            note = skill.note.replace('・','■').replace('{%1}',skill.lvs[skill.calcSkillLv].value);
            attackTarget = skill.condAttackTarget;
            atkWep += Number(skill.lvs[skill.calcSkillLv].value) * skill.stackVal;
            if (skill.effectAdd == "防御無視") flgIgnoreDef = true;
            if (skill.applyBurst != "") opBurst = 0.5; // フルバースト補正
            if (skill.applyCharge != "") opChargeComp += skill.applyCharge - 100; // チャージダメージ補正(設定値がベースの100込みなので100を引いておく)
            skill.useSkillInvalid = false;
            flgCalc = true;
          
          } else if (skill.effect == "追加ダメージ(最終攻撃力)" && skill.useSkillCheck && (skill.condAttack == damage.attackCls  || skill.condAttack == "")) {
            note = skill.note.replace('・','■').replace('・','■').replace('{%1}',skill.lvs[skill.calcSkillLv].value);
            attackTarget = skill.condAttackTarget;
            atkWep += Number(skill.lvs[skill.calcSkillLv].value) * skill.stackVal;
            if (skill.effectAdd == "防御無視") flgIgnoreDef = true;
            skill.useSkillInvalid = false;
            flgCalc = true;
            flgAddDamage = true;

          } else if (skill.effect == "追加ダメージ(最終攻撃力)" && skill.useSkillCheck) {
            //skill.useSkillInvalid = true; // 画面で設定した攻撃方法とスキル発動条件の攻撃方法が異なる場合はスキルを無効表示にする
          };

        } else {
          //skill.useSkillInvalid = true; // 画面で設定した攻撃方法とスキル発動条件の攻撃方法が異なる場合はスキルを無効表示にする
        }
        
        // 以降は計算対象の場合のみ実行
        if (flgCalc) {
          // HP関連
          if (damage.class == "攻撃型") {
            prmMaxHp = damage.prmAttack * _this.fsAtkToHpRetioAttack;
          } else if (damage.class == "支援型") {
            prmMaxHp = damage.prmAttack * _this.fsAtkToHpRetioSupport;
          } else {
            prmMaxHp = damage.prmAttack * _this.fsAtkToHpRetioDefence;
          };
        
          // スキル1,2,バーストの配列を1つに結合
          let skills = null;
          if(damage.attackerName != "") skills = damage.attackerSkills;
          damage.supporters.forEach(function(supporter){
            if(supporter.supporterName != "") {
              if (skills != null) {
                skills = skills.concat(supporter.supporterSkills);
              } else {
                skills = supporter.supporterSkills;
              };
            };
          });

          // スキル計算（先行してに計算を済ませる必要があるもの）
          if(skills != null){
            skills.forEach(function(skill){
              if(skill.useSkillCheck){
                switch (skill.effect){
                  case "最大HP":
                    maxHpBuf += Number(skill.lvs[skill.calcSkillLv].value) * skill.stackVal;
                    skill.useSkillInvalid = false;
                    break;
                  case "最大HP(回復なし)":
                    maxHpBuf += Number(skill.lvs[skill.calcSkillLv].value) * skill.stackVal;
                    skill.useSkillInvalid = false;
                    break;
                  default:
                }
              };
            });
          };
          prmMaxHp *= (1 + maxHpBuf/100);

          // スキル 計算
          if(skills != null){
            skills.forEach(function(skill){
              if(skill.useSkillCheck){
                if(attackTarget == "" || skill.condAttackTarget == attackTarget){
                  switch (skill.effect){
                    case "クリティカルダメージ":
                      if (damage.prmCritical) {
                        opCriBuf += Number(skill.lvs[skill.calcSkillLv].value) * skill.stackVal;
                        skill.useSkillInvalid = false;
                      } else {
                        //skill.useSkillInvalid = true;
                      };
                      break;
                    case "チャージダメージ":
                      if (attacker.chargeScale > 0) {
                        opCharge += Number(skill.lvs[skill.calcSkillLv].value) * skill.stackVal;
                        skill.useSkillInvalid = false;
                      } else {
                        //skill.useSkillInvalid = true;
                      };
                      break;
                    case "与えるダメージ増加":
                      if (skill.condAttackTarget == "コア" && damage.prmCoreHit) {
                        opPlus += Number(skill.lvs[skill.calcSkillLv].value) * skill.stackVal;
                        skill.useSkillInvalid = false;
                      } else if (skill.condAttackTarget != "コア") {
                        opPlus += Number(skill.lvs[skill.calcSkillLv].value) * skill.stackVal;
                        skill.useSkillInvalid = false;
                      } else {
                        //skill.useSkillInvalid = true;
                      };
                      break;
                    case "攻撃力":
                      atkBuf += Number(skill.lvs[skill.calcSkillLv].value) * skill.stackVal;
                      skill.useSkillInvalid = false;
                      break;
                    case "攻撃力(最大HP量基準)":
                      atkAdd += Number(skill.lvs[skill.calcSkillLv].value)/100 * skill.stackVal * prmMaxHp
                      skill.useSkillInvalid = false;
                      break;
                    case "攻撃力(最大攻撃力味方基準)":
                      atkBuf += Number(skill.lvs[skill.calcSkillLv].value) * skill.stackVal;
                      skill.useSkillInvalid = false;
                      break;
                    case "攻撃力(発動者基準)":
                      atkBuf += Number(skill.lvs[skill.calcSkillLv].value) * skill.stackVal;
                      skill.useSkillInvalid = false;
                      break;
                    case "有利コードダメージ増加":
                      if (damage.prmCompElement) {
                        opEleCode += Number(skill.lvs[skill.calcSkillLv].value) * skill.stackVal;
                        skill.useSkillInvalid = false;
                      } else {
                        //skill.useSkillInvalid = true;
                      };
                      break;
                    case "防御力":
                      defDbf += Number(skill.lvs[skill.calcSkillLv].value) * skill.stackVal;
                      skill.useSkillInvalid = false;
                      break;
                    default:
                  };
                } else {
                  //skill.useSkillInvalid = true;
                };
              };
            });
          };

          // 強制補正（通常攻撃以外）
          if (damage.attackCls != "通常攻撃") {
            opCharge = 100; // チャージダメージを100にする
          };

          // 強制補正（追加ダメージスキル）
          if (flgAddDamage) {
            opCri = 0; // クリティカルなし
            opCriBuf = 0; // クリバフなし
            opCore = 0; // コアヒットなし
            opRenge = 0; // 適性距離なし
          }

          // 強制補正（防御無視）
          if (flgIgnoreDef) {
            defDbf = 100; // 防御デバフを100にする
          }
          
          // 強制補正（属性有利）
          if (!damage.prmCompElement) {
            opEleCode = 0; // 有利コードを0にする
          }

          // 強制補正（防御デバフ）
          if (defDbf > 100) {
            defDbf = 100; // 防御デバフが100を超える場合は100に補正
          }

          // 計算中に0.999999999のような端数がでるため四捨五入
          atkBase = Math.round(atkBase*100)/100
          atkBuf = Math.round(atkBuf*100)/100
          atkBufOvl = Math.round(atkBufOvl*100)/100
          defEne = Math.round(defEne*100)/100
          defDbf = Math.round(defDbf*100)/100
          atkWep = Math.round(atkWep*100)/100
          opCri = Math.round(opCri*100)/100
          opCriBuf = Math.round(opCriBuf*100)/100
          opCore = Math.round(opCore*100)/100
          opRenge = Math.round(opRenge*100)/100
          opBurst = Math.round(opBurst*100)/100
          opEleCode = Math.round(opEleCode*100)/100
          opCharge = Math.round(opCharge*100)/100
          opChargeComp = Math.round(opChargeComp*100)/100
          opParts = Math.round(opParts*100)/100
          opOther = Math.round(opOther*100)/100
          opPlus = Math.round(opPlus*100)/100
          atkWep = Math.round(atkWep*100)/100
          atkWepDefIgn = Math.round(atkWepDefIgn*100)/100
          atkAdd = Math.round(atkAdd)
        
          let ret = 0;
          ret = Math.round(
            ((atkBase + atkAdd) * (1 + (atkBuf + atkBufOvl)/100)
              - defEne * ((100-defDbf)/100)
            ) * atkWep/100
            * (1 + opCri + opCriBuf/100 + opCore + opRenge + opBurst)
            * (1 + opEle + opEleCode/100)
            * ((opCharge + opChargeComp) / 100)
            * (1 + opParts/100 + opOther/100)
            * (1 + opPlus/100)
          );
          damage.calcDamage += ret;

          if (damage.calcDamageFormula != "") damage.calcDamageFormula += "<br><br>"
          damage.calcDamageFormula += 
              note + "<br>"
            + "((攻撃力" + atkBase + " + " + "攻撃力加算" + atkAdd + ") * (1 + 攻撃力バフ" + (atkBuf + atkBufOvl) + "/100)<br>"
            + "　- (敵防御力" + defEne + " * (1-防御デバフ" + defDbf + "/100))" + "<br>"
            + ") * 武器または直接ダメージ倍率" + atkWep + "/100" + "<br>"
            + "* (1 + クリ" + opCri + " + クリダメ" + opCriBuf + "/100 + コアヒット" + opCore + " + 適正距離" + opRenge + " + フルバ" + opBurst + ")" + "<br>"
            + "* (1 + 属性有利" + opEle + " + 有利コード" + opEleCode + "/100)" + "<br>"
            + "* (チャーダメ" + (opCharge + opChargeComp) + "/100)" + "<br>"
            + "* (1 + 部位ダメ" + opParts + "/100 + その他" + opOther + "/100)" + "<br>"
            + "* (1 + 受けるダメ" + opPlus + "/100)"
            + "= " + ret.toLocaleString();
        };
      });
    },

    mFsDelete(tagDamageOrder) { // 火力シミュ：一覧から行削除
      let _this = this;
      let damage = _this.damageList.find(e => e.order === tagDamageOrder);

      damage.disp = false;

      _this.damageListNum = 0;
      _this.damageList.forEach(function(damage){
        if(damage.disp)_this.damageListNum++; // 現在表示数をカウント
      });
    },

    mFsCopy(srcDamage, srcOrder) { // 火力シミュ：一覧の行コピー
      let _this = this;

      if (_this.damageListNum < _this.damageListMaxNum) {
        // 非表示行の先頭を表示に変更
        let setFlg = false;
        _this.damageList.forEach(function(damage) {
          if (setFlg == false && damage.disp == false) {
            damage.disp = true;
            damage.order = srcOrder + 0.5; // 暫定で0.5刻みで付けておいて後で一式付けなおす
            damage.attackerName = srcDamage.attackerName;
            damage.attackerSkill1Ct = srcDamage.attackerSkill1Ct;
            damage.attackerSkill2Ct = srcDamage.attackerSkill2Ct;
            damage.attackerBurstCt = srcDamage.attackerBurstCt;
            damage.attackerSkillLv[1] = srcDamage.attackerSkillLv[1];
            damage.attackerSkillLv[2] = srcDamage.attackerSkillLv[2];
            damage.attackerSkillLv[3] = srcDamage.attackerSkillLv[3];

            damage.attackerSkills.splice(0) // 配列全削除
            srcDamage.attackerSkills.forEach(function(skill){
              let newSkill = JSON.parse(JSON.stringify(_this.fsUseSkillProt));
              newSkill.id = skill.id;
              newSkill.note = skill.note;
              newSkill.titleFlg = skill.titleFlg;
              newSkill.skillCls = skill.skillCls;
              newSkill.skillTarget = skill.skillTarget;
              newSkill.effect = skill.effect;
              newSkill.effectAdd = skill.effectAdd;
              skill.lvs.forEach(function(lv){
                let newLv = JSON.parse(JSON.stringify(_this.fsLevelProt));
                newLv.value = lv.value;
                newSkill.lvs.push(newLv);
              });
              newSkill.stackMax = skill.stackMax;
              newSkill.stackVal = skill.stackVal;
              newSkill.condAttack = skill.condAttack;
              newSkill.condAttackTarget = skill.condAttackTarget;
              newSkill.condAllyWeapon = skill.condAllyWeapon;
              newSkill.condAllyElement = skill.condAllyElement;
              newSkill.condAllyClass = skill.condAllyClass;
              newSkill.condEnemyClass = skill.condEnemyClass;
              newSkill.applyBurst = skill.applyBurst;
              newSkill.useSkillFlg = skill.useSkillFlg;
              newSkill.useSkillCheck = skill.useSkillCheck;
              damage.attackerSkills.push(newSkill);
            });

            damage.attackList.splice(0) // 配列全削除
            srcDamage.attackList.forEach(function(attack){
              damage.attackList.push(attack);
            });

            damage.attackCls = srcDamage.attackCls;
            //damage.attackTarget = srcDamage.attackTarget;
            damage.prmCoreHit = srcDamage.prmCoreHit;
            damage.prmFullBurst = srcDamage.prmFullBurst;
            damage.prmCritical = srcDamage.prmCritical;
            damage.prmCompElement = srcDamage.prmCompElement;
            damage.prmCampFar = srcDamage.prmCampFar;
            damage.prmAttack = srcDamage.prmAttack;
            damage.prmEnemyDef = srcDamage.prmEnemyDef;
            damage.prmAttackDmgUp = srcDamage.prmAttackDmgUp.slice(0,4);
            damage.prmElementDmgUp = srcDamage.prmElementDmgUp.slice(0,5);
            damage.prmCriticalDmgUp = srcDamage.prmCriticalDmgUp.slice(0,4);
            damage.prmChargeDmgUp = srcDamage.prmChargeDmgUp.slice(0,4);

            for(let i = 0; i <= 3; ++i){
              damage.supporters[i].id = srcDamage.supporters[i].id;
              damage.supporters[i].order = srcDamage.supporters[i].order;
              damage.supporters[i].supporterName = srcDamage.supporters[i].supporterName;
              damage.supporters[i].supporterSkillLv[1] = srcDamage.supporters[i].supporterSkillLv[1];
              damage.supporters[i].supporterSkillLv[2] = srcDamage.supporters[i].supporterSkillLv[2];
              damage.supporters[i].supporterSkillLv[3] = srcDamage.supporters[i].supporterSkillLv[3];

              damage.supporters[i].supporterSkills.splice(0) // 配列全削除
              srcDamage.supporters[i].supporterSkills.forEach(function(skill){
                let newSkill = JSON.parse(JSON.stringify(_this.fsUseSkillProt));
                newSkill.id = skill.id;
                newSkill.note = skill.note;
                newSkill.titleFlg = skill.titleFlg;
                newSkill.skillCls = skill.skillCls;
                newSkill.skillTarget = skill.skillTarget;
                newSkill.effect = skill.effect;
                newSkill.effectAdd = skill.effectAdd;
                skill.lvs.forEach(function(lv){
                  let newLv = JSON.parse(JSON.stringify(_this.fsLevelProt));
                  newLv.value = lv.value;
                  newSkill.lvs.push(newLv);
                });
                newSkill.stackMax = skill.stackMax;
                newSkill.stackVal = skill.stackVal;
                newSkill.condAttack = skill.condAttack;
                newSkill.condAttackTarget = skill.condAttackTarget;
                newSkill.condAllyWeapon = skill.condAllyWeapon;
                newSkill.condAllyElement = skill.condAllyElement;
                newSkill.condAllyClass = skill.condAllyClass;
                newSkill.condEnemyClass = skill.condEnemyClass;
                newSkill.applyBurst = skill.applyBurst;
                newSkill.useSkillFlg = skill.useSkillFlg;
                newSkill.useSkillCheck = skill.useSkillCheck;
                damage.supporters[i].supporterSkills.push(newSkill);
              });
            }
  
            damage.calcDamage = srcDamage.calcDamage;
            damage.calcDamageFormula = srcDamage.calcDamageFormula;

            //dstId = damage.id;
            setFlg = true;
          }
        });

        // 表示順でソート
        _this.damageList.sort(function(first, second){
          if (first.order > second.order){
            return 1;
          }else if (first.order < second.order){
            return -1;
          }else{
            return 0;
          }
        });

        let cnt = 1;
        _this.damageListNum = 0
        _this.damageList.forEach(function(damage) {
          damage.order = cnt; // 表示順を1からの整数で付けなおし
          if(damage.disp)_this.damageListNum++; // 現在表示数をカウント
          cnt++;
        });
      }
    },

    mSmGraphRightChange() { // 散布図メーカー：左コメント変更イベント
      let len = this.smGraphRight.length;
      if (!this.isSmartPhone) {
        this.smGraphRightTop = 34 - (len - 5) * 1.2;
      } else {
        this.smGraphRightTop = 8 - (len - 5) * 1.2;
      }
    },

    mSmGraphLeftChange() { // 散布図メーカー：左コメント変更イベント
      let len = this.smGraphLeft.length;
      if (!this.isSmartPhone) {
        this.smGraphLeftTop = 34 - (len - 5) * 1.2;
      } else {
        this.smGraphLeftTop = 8 - (len - 5) * 1.2;
      }
    },

    mSmDownload() { // 散布図メーカー：画像ダウンロード
      html2canvas(document.getElementById('smGraphGrid')).then(canvas => {
        var result = document.querySelector("#smDownload");
        result.innerHTML = '';
        result.appendChild(canvas)

        let link = document.createElement("a");
        link.href = canvas.toDataURL("image/png");
        link.download = "nikke-scatter.png";
        link.click();
      });
    },

    mSelectTab(idx) {
      this.$cookies.set('coSelectTab', idx, 0);
      this.coSelTab = idx;
      this.mReset(); // パック一覧の絞り込みをクリア
      this.mClearQfFil(); // ニケの絞り込みをクリア
    },

    mLsPackCheck(id, chkOn) {
      let _this = this;
      this.packs.forEach(function(pack){
        if(pack.id == id) {
          pack.check = pack.check * (-1);

          if (chkOn) {
            if (_this.selected[pack.id] === undefined) {
              _this.selected[pack.id] = true;
            } else {
              _this.selected[pack.id] = !_this.selected[pack.id];
            }
          }
        }
      })
      this.mCalcLeveling();
    },

    mCalcLeveling() { // レベリング計算
      let _this = this;

      this.needs[0].NextLv = this.coNikkeLv + 1;
      this.needs[1].NextLv = Math.floor(this.coNikkeLv / 20) * 20 + 21;
      this.needs[2].NextLv = this.needs[1].NextLv + 20;

      // リスト上でチェックされたパックから追加分の資源を読み取り
      let creditDayNum = 0;
      let creditWeekNum = 0;
      let creditMonthNum = 0;
      let creditDayCase = 0;
      let creditWeekCase = 0;
      let creditMonthCase = 0;
      let coreDayNum = 0;
      let coreWeekNum = 0;
      let coreMonthNum = 0;
      let coreDayCase = 0;
      let coreWeekCase = 0;
      let coreMonthCase = 0;
      let battleDayNum = 0;
      let battleWeekNum = 0;
      let battleMonthNum = 0;
      let battleDayCase = 0;
      let battleWeekCase = 0;
      let battleMonthCase = 0;
      this.packs.forEach(function(pack){
        if (pack.check == true) {
          if (pack.division == "デイリー") {
            creditDayCase += pack.items.CaseCredit;
            creditDayNum += pack.items.Credit * 1000;
            coreDayCase += pack.items.CaseCore;
            coreDayNum += pack.items.Core;
            battleDayCase += pack.items.CaseBattle;
            battleDayNum += pack.items.Battle * 1000;
          } else if (pack.division == "ウィークリー") {
            creditWeekCase += pack.items.CaseCredit;
            creditWeekNum += pack.items.Credit * 1000;
            coreWeekCase += pack.items.CaseCore;
            coreWeekNum += pack.items.Core;
            battleWeekCase += pack.items.CaseBattle;
            battleWeekNum += pack.items.Battle * 1000;
          } else if (pack.division == "マンスリー") {
            creditMonthCase += pack.items.CaseCredit;
            creditMonthNum += pack.items.Credit * 1000;
            coreMonthCase += pack.items.CaseCore;
            coreMonthNum += pack.items.Core;
            battleMonthCase += pack.items.CaseBattle;
            battleMonthNum += pack.items.Battle * 1000;
          } else if (pack.name == "30-DAY成長補給品") {
            coreDayCase += 10;
          }
        }
      });

      // 経験値リストから現在レベルの蓄積経験値と目標レベルの蓄積経験値を取得
      var expNow = this.experience.find(e => e.Lv === this.coNikkeLv);

      for (let i=0; i <= 2; ++i) {
        if (this.needs[i].NextLv <= this.experience.length) {
          let expNext = this.experience.find(e => e.Lv === this.needs[i].NextLv);
          this.needs[i].NeedCredit = expNext.Credit - expNow.Credit - (this.lsPosCaseCredit * this.coRewardCredit) - (this.lsPosCredit * 1000);
          this.needs[i].NeedBattle = expNext.Battle - expNow.Battle - (this.lsPosCaseBattle * this.coRewardBattle) - (this.lsPosBattle * 1000);
          this.needs[i].NeedCore   = expNext.Core   - expNow.Core   - (this.lsPosCaseCore   * this.coRewardCore)   - this.lsPosCore;
        }
      }

      // クレジットの所要時間（次レベルまでの残り時間を計算）
      let flgCredit = false;
      let flgBattle = false;
      let flgCore = false;
      let lpCnt = -1;
      if (this.needs[0].NextLv <= this.experience.length) lpCnt++; // 次のレベルがデータテーブルの範囲内
      if (this.needs[1].NextLv <= this.experience.length) lpCnt++; // 20レベル区切りの次のレベルがデータテーブルの範囲内
      if (this.needs[2].NextLv <= this.experience.length) lpCnt++; // 20レベル区切りの次の次のレベルがデータテーブルの範囲内

      for (let i = 0; i <= lpCnt; ++i) {
        let nextday = new Date();
        let nextdayStr = "";
        let preCredit = this.needs[i].NeedCredit;
        let preBattle = this.needs[i].NeedBattle;
        let preCore = this.needs[i].NeedCore;

        this.needs[i].HourCredit = 0;
        this.needs[i].HourBattle = 0;
        this.needs[i].HourCore = 0;
        this.needs[i].NeedYen = 0;
        
        this.needs[i].LogCredit = "";
        this.needs[i].LogCredit += "★クレジット";
        this.needs[i].LogCredit += "<br>＜必要量＞";
        this.needs[i].LogCredit += "<br>　Lv" + this.coNikkeLv + "⇒" + this.needs[i].NextLv + "　" + Math.floor((this.needs[i].NeedCredit + (this.lsPosCaseCredit * this.coRewardCredit) + Number(this.lsPosCredit * 1000))/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "<br>＜収入＞";
        this.needs[i].LogCredit += "<br>　所持　ケース+個別：" + Math.floor(((this.lsPosCaseCredit * this.coRewardCredit) + (this.lsPosCredit * 1000))/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "<br>　毎日　防御報酬：" + Math.floor(Math.floor(this.coRewardCredit*24)/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "、殲滅+ミッション：" + Math.floor((Math.floor(this.lsWipeouts * this.coRewardCredit * 2) + (1 * Math.floor(this.coRewardCredit)))/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "、課金：" + Math.floor((creditDayCase * Math.floor(this.coRewardCredit) + creditDayNum)/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "<br>　毎週　ミッション：" + Math.floor(Math.floor((8 * this.coRewardCredit) * 2)/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "、鉄くず：" + Math.floor((18 * Math.floor(this.coRewardCredit) + 500000)/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "、課金：" + Math.floor((creditWeekCase * Math.floor(this.coRewardCredit) + creditWeekNum)/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "<br>　毎月　課金：" + Math.floor((creditMonthCase * Math.floor(this.coRewardCredit) + creditMonthNum)/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "<br>＜目標レベルまでの必要量推移＞";
        this.needs[i].LogCredit += "<br>　" + nextday.getFullYear() + "/" + ("00"+(nextday.getMonth()+1)).slice(-2) + "/" + ("00"+nextday.getDate()).slice(-2) + " " + ("00"+nextday.getHours()).slice(-2) + "時(" + [ "日", "月", "火", "水", "木", "金", "土" ][nextday.getDay()] + ")";
        this.needs[i].LogCredit += "　" + Math.floor(this.needs[i].NeedCredit/1000).toLocaleString() + "K";

        this.needs[i].LogBattle = "";
        this.needs[i].LogBattle += "★バトルデータ";
        this.needs[i].LogBattle += "<br>＜必要量＞";
        this.needs[i].LogBattle += "<br>　Lv" + this.coNikkeLv + "⇒" + this.needs[i].NextLv + "　" + Math.floor((this.needs[i].NeedBattle + (this.lsPosCaseBattle * this.coRewardBattle) + Number(this.lsPosBattle * 1000))/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "<br>＜収入＞";
        this.needs[i].LogBattle += "<br>　所持　ケース+個別：" + Math.floor(((this.lsPosCaseBattle * this.coRewardBattle) + (this.lsPosBattle * 1000))/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "<br>　毎日　防御報酬：" + Math.floor(this.coRewardBattle*24/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "、殲滅+シミュレーション：" + Math.floor((Math.floor(this.lsWipeouts * this.coRewardBattle * 2) + (18 * Math.floor(this.coRewardBattle)))/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "、課金：" + Math.floor((battleDayCase * Math.floor(this.coRewardBattle) + battleDayNum)/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "<br>　毎週　鉄くず：" + Math.floor((36 * Math.floor(this.coRewardBattle))/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "、課金：" + Math.floor((battleMonthCase * Math.floor(this.coRewardBattle) + battleMonthNum)/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "<br>　毎月　課金：" + Math.floor((battleMonthCase * Math.floor(this.coRewardBattle) + battleMonthNum)/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "<br>＜目標レベルまでの必要量推移＞";
        this.needs[i].LogBattle += "<br>　" + nextday.getFullYear() + "/" + ("00"+(nextday.getMonth()+1)).slice(-2) + "/" + ("00"+nextday.getDate()).slice(-2) + " " + ("00"+nextday.getHours()).slice(-2) + "時(" + [ "日", "月", "火", "水", "木", "金", "土" ][nextday.getDay()] + ")";
        this.needs[i].LogBattle += "　" + Math.floor(this.needs[i].NeedBattle/1000).toLocaleString() + "K";

        this.needs[i].LogCore = "";
        this.needs[i].LogCore += "★コアダスト";
        this.needs[i].LogCore += "<br>＜必要量＞";
        this.needs[i].LogCore += "<br>　Lv" + this.coNikkeLv + "⇒" + this.needs[i].NextLv + "　" + Math.floor(this.needs[i].NeedCore + (this.lsPosCaseCore * this.coRewardCore) + Number(this.lsPosCore)).toLocaleString();
        this.needs[i].LogCore += "<br>＜収入＞";
        this.needs[i].LogCore += "<br>　所持　ケース+個別：" + Math.floor((this.lsPosCaseCore * this.coRewardCore) + this.lsPosCore).toLocaleString();
        this.needs[i].LogCore += "<br>　毎日　防御報酬：" + Math.floor(this.coRewardCore*24).toLocaleString();
        this.needs[i].LogCore += "、殲滅+ミッション：" + (Math.floor(this.lsWipeouts * this.coRewardCore * 2) + 50).toLocaleString();
        this.needs[i].LogCore += "、課金：" + (coreDayCase * Math.floor(this.coRewardCore) + coreDayNum).toLocaleString();
        this.needs[i].LogCore += "<br>　毎週　鉄くず：" + (36 * Math.floor(this.coRewardCore)).toLocaleString();
        this.needs[i].LogCore += "、課金：" + (coreWeekCase * Math.floor(this.coRewardCore) + coreWeekNum).toLocaleString();
        this.needs[i].LogCore += "<br>　毎月　課金：" + (coreMonthCase * Math.floor(this.coRewardCore) + coreMonthNum).toLocaleString();
        this.needs[i].LogCore += "<br>＜目標レベルまでの必要量推移＞";
        this.needs[i].LogCore += "<br>　" + nextday.getFullYear() + "/" + ("00"+(nextday.getMonth()+1)).slice(-2) + "/" + ("00"+nextday.getDate()).slice(-2) + " " + ("00"+nextday.getHours()).slice(-2) + "時(" + [ "日", "月", "火", "水", "木", "金", "土" ][nextday.getDay()] + ")";
        this.needs[i].LogCore += "　" + Math.floor(this.needs[i].NeedCore).toLocaleString();

        while (this.needs[i].NeedCredit > 0 || this.needs[i].NeedBattle > 0 || this.needs[i].NeedCore > 0) {
          nextday.setHours(nextday.getHours() + 1); // +1h
          nextdayStr = nextday.getFullYear() + "/" + ("00"+(nextday.getMonth()+1)).slice(-2) + "/" + ("00"+nextday.getDate()).slice(-2) + " " + ("00"+nextday.getHours()).slice(-2) + "時(" + [ "日", "月", "火", "水", "木", "金", "土" ][nextday.getDay()] + ")"
          flgCredit = false;
          flgBattle = false;
          flgCore = false;

          // クレジット計算
          if (this.needs[i].NeedCredit > 0) {
            flgCredit = true;
            this.needs[i].HourCredit++; // 残り時間を+1
            this.needs[i].NeedCredit -= this.coRewardCredit; // 必要経験値から1時間分の基地防御報酬を引く
            if (nextday.getHours() == 5) { // 5時を迎えた時にデイリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedCredit -= (creditDayCase * Math.floor(this.coRewardCredit)) // デイリー系課金アイテム(ケース)
                                         + creditDayNum // デイリー系課金アイテム(個)
                                         + Math.floor(this.lsWipeouts * this.coRewardCredit * 2) // まとめて殲滅
                                         + (1 * Math.floor(this.coRewardCredit)); // デイリーミッション
              this.needs[i].LogCredit += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedCredit/1000).toLocaleString() + "K　デイリー（" + Math.floor((this.needs[i].NeedCredit - preCredit)/1000).toLocaleString() + "K）";
              preCredit = this.needs[i].NeedCredit;
            }
            if (nextday.getHours() == 5 && nextday.getDay() == 2 && this.needs[i].NeedCredit > 0) { // 火曜の5時を迎えた時にウィークリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedCredit -= (creditWeekCase * Math.floor(this.coRewardCredit)) // ウィークリー系課金アイテム(ケース)
                                        + creditWeekNum // ウィークリー系課金アイテム(個)
                                        + Math.floor((8 * this.coRewardCredit)) * 2 // ウィークリーミッション
                                        + (18 * Math.floor(this.coRewardCredit) + 500000); // 鉄くずショップ
              this.needs[i].LogCredit += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedCredit/1000).toLocaleString() + "K　ウィークリー（" + Math.floor((this.needs[i].NeedCredit - preCredit)/1000).toLocaleString() + "K）";
              preCredit = this.needs[i].NeedCredit;
            }
            if (nextday.getHours() == 5 && nextday.getDate() == 1 && this.needs[i].NeedCredit > 0) { // 月初めの5時を迎えた時にマンスリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedCredit -= (creditMonthCase * Math.floor(this.coRewardCredit)) // マンスリー系課金アイテム(ケース)
                                        + creditMonthNum; // マンスリー系課金アイテム(個)
              this.needs[i].LogCredit += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedCredit/1000).toLocaleString() + "K　マンスリー（" + Math.floor((this.needs[i].NeedCredit - preCredit)/1000).toLocaleString() + "K）";
              preCredit = this.needs[i].NeedCredit;
            }
            if (nextday.getHours() != 5 && this.needs[i].NeedCredit <= 0) {
              this.needs[i].LogCredit += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedCredit/1000).toLocaleString() + "K　デイリー（" + Math.floor((this.needs[i].NeedCredit - preCredit)/1000).toLocaleString() + "K）";
            }
          }
          
          // バトルデータ計算
          if (this.needs[i].NeedBattle > 0) {
            flgBattle = true;
            this.needs[i].HourBattle++; // 残り時間を+1
            this.needs[i].NeedBattle -= this.coRewardBattle; // 必要経験値から1時間分の基地防御報酬を引く
            if (nextday.getHours() == 5) { // 5時を迎えた時にデイリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedBattle -= (battleDayCase * Math.floor(this.coRewardBattle)) // デイリー系課金アイテム(ケース)
                                        + battleDayNum // デイリー系課金アイテム(個)
                                        + Math.floor(this.lsWipeouts * this.coRewardBattle * 2) // まとめて殲滅
                                        + (18 * Math.floor(this.coRewardBattle)); // シミュレーションルーム5-Cクリア
              this.needs[i].LogBattle += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedBattle/1000).toLocaleString() + "K　デイリー（" + Math.floor((this.needs[i].NeedBattle - preBattle)/1000).toLocaleString() + "K）";
              preBattle = this.needs[i].NeedBattle;
            }
            if (nextday.getHours() == 5 && nextday.getDay() == 2 && this.needs[i].NeedBattle > 0) { // 火曜の5時を迎えた時にウィークリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedBattle -= (battleWeekCase * Math.floor(this.coRewardBattle)) // ウィークリー系課金アイテム(ケース)
                                        + battleWeekNum // デイリー系課金アイテム(個)
                                        + (36 * Math.floor(this.coRewardBattle)); // 鉄くずショップ
              this.needs[i].LogBattle += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedBattle/1000).toLocaleString() + "K　ウィークリー（" + Math.floor((this.needs[i].NeedBattle - preBattle)/1000).toLocaleString() + "K）";
              preBattle = this.needs[i].NeedBattle;
            }
            if (nextday.getHours() == 5 && nextday.getDate() == 1 && this.needs[i].NeedBattle > 0) { // 月初めの5時を迎えた時にマンスリー系アイテムによる獲得経験値を必要経験値から引く
              this.needs[i].NeedBattle -= (battleMonthCase * Math.floor(this.coRewardBattle)) // マンスリー系課金アイテム(ケース)
                                        + battleMonthNum; // マンスリー系課金アイテム(個)
              this.needs[i].LogBattle += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedBattle/1000).toLocaleString() + "K　マンスリー（" + Math.floor((this.needs[i].NeedBattle - preBattle)/1000).toLocaleString() + "K）";
              preBattle = this.needs[i].NeedBattle;
            }
            if (nextday.getHours() != 5 && this.needs[i].NeedBattle <= 0) {
              this.needs[i].LogBattle += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedBattle/1000).toLocaleString() + "K　デイリー（" + Math.floor((this.needs[i].NeedBattle - preBattle)/1000).toLocaleString() + "K）";
            }
          }
          
          // コアダスト計算
          if (this.needs[i].NeedCore > 0) {
            flgCore = true;
            this.needs[i].HourCore++; // 残り時間を+1
            this.needs[i].NeedCore -= this.coRewardCore; // 必要経験値から1時間分の基地防御報酬を引く
            if (nextday.getHours() == 5) { // 5時を迎えた時にデイリー系アイテムによる獲得経験値を必要経験値から引く
              let pre = this.needs[i].NeedCore;
              this.needs[i].NeedCore -= (coreDayCase * Math.floor(this.coRewardCore)) // デイリー系課金アイテム(ケース)
                                      + coreDayNum // デイリー系課金アイテム(個)
                                      + Math.floor(this.lsWipeouts * this.coRewardCore * 2) // まとめて殲滅
                                      + 50; // デイリーミッション
              this.needs[i].LogCore += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedCore).toLocaleString() + "　デイリー（" + Math.floor(this.needs[i].NeedCore - preCore).toLocaleString() + "）";
              preCore = this.needs[i].NeedCore;
            }
            if (nextday.getHours() == 5 && nextday.getDay() == 2 && this.needs[i].NeedCore > 0) { // 火曜の5時を迎えた時にウィークリー系アイテムによる獲得経験値を必要経験値から引く
              let pre = this.needs[i].NeedCore;
              this.needs[i].NeedCore -= (coreWeekCase * Math.floor(this.coRewardCore)) // ウィークリー系課金アイテム(ケース)
                                      + coreWeekNum // デイリー系課金アイテム(個)
                                      + (36 * Math.floor(this.coRewardCore)) // 鉄くずショップ
              this.needs[i].LogCore += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedCore).toLocaleString() + "　ウィークリー（" + Math.floor(this.needs[i].NeedCore - preCore).toLocaleString() + "）";
              preCore = this.needs[i].NeedCore;
            }
            if (nextday.getHours() == 5 && nextday.getDate() == 1 && this.needs[i].NeedCore > 0) { // 月初めの5時を迎えた時にマンスリー系アイテムによる獲得経験値を必要経験値から引く
              let pre = this.needs[i].NeedCore;
              this.needs[i].NeedCore -= (coreMonthCase * Math.floor(this.coRewardCore)) // マンスリー系課金アイテム(ケース)
                                      + coreMonthNum; // マンスリー系課金アイテム(個)
              this.needs[i].LogCore += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedCore).toLocaleString() + "　マンスリー（" + Math.floor(this.needs[i].NeedCore - preCore).toLocaleString() + "）";
              preCore = this.needs[i].NeedCore;
            }
            if (nextday.getHours() != 5 && this.needs[i].NeedCore <= 0) {
              this.needs[i].LogCore += "<br>　" + nextdayStr + "　" + Math.floor(this.needs[i].NeedCore).toLocaleString() + "　デイリー（" + Math.floor(this.needs[i].NeedCore - preCore).toLocaleString() + "）";
            }
          }

          // 課金額計算
          if (nextday.getHours() == 5) {
            if (flgCredit || flgBattle || flgCore) {
              //if (this.needs[i].NeedCredit >= 0 && this.needs[i].NeedBattle >= 0 && this.needs[i].NeedCore >= 0) {
                this.packs.forEach(function(pack){
                  if (pack.check == true) {
                    // 5時を迎えた時
                    if (nextday.getHours() == 5 && pack.division == "デイリー") {
                      if (flgCredit && (pack.items.CaseCredit >= 1 || pack.items.Credit)) {
                        _this.needs[i].NeedYen += pack.price;
                      } else if (flgBattle && (pack.items.CaseBattle >= 1 || pack.items.Battle)) {
                        _this.needs[i].NeedYen += pack.price;
                      } else if (flgCore && (pack.items.CaseCore >= 1 || pack.items.Core)) {
                        _this.needs[i].NeedYen += pack.price;
                      }
                    }

                    // 火曜の5時を迎えた時
                    if (nextday.getHours() == 5 && nextday.getDay() == 2 && pack.division == "ウィークリー") {
                      if (flgCredit && (pack.items.CaseCredit >= 1 || pack.items.Credit)) {
                        _this.needs[i].NeedYen += pack.price;
                      } else if (flgBattle && (pack.items.CaseBattle >= 1 || pack.items.Battle)) {
                        _this.needs[i].NeedYen += pack.price;
                      } else if (flgCore && (pack.items.CaseCore >= 1 || pack.items.Core)) {
                        _this.needs[i].NeedYen += pack.price;
                      }
                    }
                    
                    // 月初めの5時を迎えた時
                    if (nextday.getHours() == 5 && nextday.getDate() == 1 && pack.division == "マンスリー") {
                      if (flgCredit && (pack.items.CaseCredit >= 1 || pack.items.Credit)) {
                        _this.needs[i].NeedYen += pack.price;
                      } else if (flgBattle && (pack.items.CaseBattle >= 1 || pack.items.Battle)) {
                        _this.needs[i].NeedYen += pack.price;
                      } else if (flgCore && (pack.items.CaseCore >= 1 || pack.items.Core)) {
                        _this.needs[i].NeedYen += pack.price;
                      }
                    }
                    
                    // 月初めの5時を迎えた時
                    if (nextday.getHours() == 5 && nextday.getDate() == 1 && pack.name == "30-DAY成長補給品") {
                      if (flgCore) {
                        _this.needs[i].NeedYen += pack.price;
                      }
                    }
                  }
                })
              //}
            }
          }
        };

        // 所要日数
        this.needs[i].DayCredit = this.needs[i].HourCredit / 24;
        this.needs[i].DayBattle = this.needs[i].HourBattle / 24;
        this.needs[i].DayCore   = this.needs[i].HourCore / 24;

        // 所要日数グラフ
        this.needs[i].DayCreditPer = Math.floor(this.needs[i].DayCredit / (Math.max(this.needs[i].DayCredit, this.needs[i].DayBattle, this.needs[i].DayCore)*1.2) * 100)
        this.needs[i].DayBattlePer = Math.floor(this.needs[i].DayBattle / (Math.max(this.needs[i].DayCredit, this.needs[i].DayBattle, this.needs[i].DayCore)*1.2) * 100)
        this.needs[i].DayCorePer   = Math.floor(this.needs[i].DayCore   / (Math.max(this.needs[i].DayCredit, this.needs[i].DayBattle, this.needs[i].DayCore)*1.2) * 100)

        // 最短日数
        let today = new Date();
        let maxHour = Math.max(this.needs[i].HourCredit, this.needs[i].HourBattle, this.needs[i].HourCore);
        today.setHours(today.getHours() + maxHour);
        this.needs[i].ArrivalDate = (today.getFullYear()+"").slice(-2) + "/" + ("00"+(today.getMonth()+1)).slice(-2) + "/" + ("00"+today.getDate()).slice(-2) + "(" + [ "日", "月", "火", "水", "木", "金", "土" ][today.getDay()] + ")" + ("00"+today.getHours()).slice(-2) + "時";

        // 現在日時
        this.lsToday = new Date();
        this.lsToday = "現在：" + (this.lsToday.getFullYear()+"").slice(-2) + "/" + ("00"+(this.lsToday.getMonth()+1)).slice(-2) + "/" + ("00"+this.lsToday.getDate()).slice(-2) + "(" + [ "日", "月", "火", "水", "木", "金", "土" ][this.lsToday.getDay()] + ")" + ("00"+this.lsToday.getHours()).slice(-2) + "時";
      }

      for (let i = lpCnt+1; i <= 2; ++i) {
        // 所要日数
        this.needs[i].HourCredit = 0;
        this.needs[i].HourBattle = 0;
        this.needs[i].HourCore = 0;
        this.needs[i].DayCredit = 0;
        this.needs[i].DayBattle = 0;
        this.needs[i].DayCore = 0;
        this.needs[i].DayCreditPer = 0;
        this.needs[i].DayBattlePer = 0;
        this.needs[i].DayCorePer = 0;
        this.needs[i].ArrivalDate = "必要資材データ無し";
        this.needs[i].NeedYen = 0;
      }

    },

    mChangeNikkeLv() { // ニケレベル変更
      if (this.coNikkeLv < 1) {
        this.coNikkeLv = 1;
      } else if (this.coNikkeLv > this.experience.length) {
        this.coNikkeLv = this.experience.length;
      } else {
        this.coNikkeLv = Math.floor(this.coNikkeLv);
      }
      this.$cookies.set('coNikkeLv', this.coNikkeLv, 0);
      this.mCalcLeveling();
    },

    mChangeRewardLv() { // 基地防御レベル変更
      if (this.coRewardLv < 1) {
        this.coRewardLv = 1;
      } else if (this.coRewardLv > this.reward.length) {
        this.coRewardLv = this.reward.length;
      } else {
        this.coRewardLv = Math.floor(this.coRewardLv);
      }
      var found = this.reward.find(e => e.Lv === this.coRewardLv);
      this.coRewardCredit = Math.floor(found.Credit*1.6);
      this.coRewardBattle = Math.floor(found.Battle*1.45);
      this.coRewardCore = Math.floor(found.Core*1.45 * Math.pow(10,2)) / Math.pow(10,2);
      this.$cookies.set('coRewardLv', this.coRewardLv, 0);
      this.$cookies.set('coRewardCredit', this.coRewardCredit, 0);
      this.$cookies.set('coRewardBattle', this.coRewardBattle, 0);
      this.$cookies.set('coRewardCore', this.coRewardCore, 0);
      this.mCalcLeveling();
    },

