    mMenu(menuName, btnName, helpName) { // メニュー表示ON/OFF
      let menu = document.getElementById(menuName);
      let menuBtn = document.getElementById(btnName);
      let helpEle = document.getElementById(helpName);
      if (menu.style.display == "") {
        menu.style.display = "none";
        menuBtn.open = ""
        helpEle.style.display = "none";
      } else {
        menu.style.display = ""
        menuBtn.open = "open"
        helpEle.style.display = "";
      }
    },

    mSaveQf(tag) { // クッキーへ部隊欄を保存
      let buf = "";
      for (let i = 1; i <= 6; ++i) {
      let squad = document.getElementById("squad"+i);
        squad.childNodes.forEach(function(item){
          buf = buf + item.lang + ",";
        });
        this.$cookies.set('squad'+i+"_"+tag, buf, 0);
      };
    },

    mLoadQf(tag) { // クッキーから部隊欄を読み取り再現する
      this.sqSquadSelect = tag;
      this.mSfClearNikke(false);
      let buf = "";
      let menbers = [];
      let candidate = document.getElementById("sfCandidate");
      for (let i = 1; i <= 6; ++i) {
        buf = this.$cookies.get('squad'+i+"_"+tag);
        let squad = document.getElementById("squad" + i);
        if (buf) {
          menbers = buf.split(",");
          menbers.forEach(function(menber){
            candidate.childNodes.forEach(function(item){
              if (item.lang == menber) squad.appendChild(item);
            });
          });
        }
      }
    },

    mSfClearNikke(flg) { // 部隊編成の部隊欄から候補一覧へニケを戻す
      let ret = true;
      if (flg) {
        ret = window.confirm('部隊の配置をクリアしますが宜しいですか？');
      }

      if (ret) {
        let candidate = document.getElementById("sfCandidate");
        for (let i = 1; i <= 6; ++i) {
          let squad = document.getElementById("squad" + i);
          while (squad.childNodes.length != 0) {
            squad.childNodes.forEach(function(member){
              let idx = 0; // ドロップ先の要素番号
              let cnt = 0;
              // ドロップ先の画像のID＞ドロップする画像のIDとなる画像を探す
              candidate.childNodes.forEach(function(item){
                if(item.id != undefined && item.id.includes("nikke")) {
                  if(item.id <= member.id) idx = cnt+1;
                  cnt++;
                }
              });
              candidate.insertBefore(member, candidate.childNodes.item(idx)); // ドロップ先画像の1つ前に追加
            });
          };
        };
        if (flg) {
          this.mSaveQf(this.sqSquadSelect);
        }
      };
    },

    mSmClearNikke(msg) { // 散布図メーカーのグラフから候補一覧へニケを戻す
      let ret = false;
      if (msg != "") {
        ret = window.confirm(msg);
      }

      if (ret) {
        let candidate = document.getElementById("smCandidate");
        let graph = document.getElementById("smGraph");
        let flgFind = true;
        while (flgFind) {
          flgFind = false;
          graph.childNodes.forEach(function(member){
            if ((member.id != undefined) && (member.id.includes("nikke"))) { // idがnullの要素(上下左右の表題)はスキップ
              flgFind = true;
              let idx = 0; // ドロップ先の要素番号
              let cnt = 0;
              // ドロップ先の画像のID＞ドロップする画像のIDとなる画像を探す
              candidate.childNodes.forEach(function(item){
                if(item.id != undefined && item.id.includes("nikke")) {
                  if(item.id <= member.id) idx = cnt+1;
                  cnt++;
                }
              });
              candidate.insertBefore(member, candidate.childNodes.item(idx)); // ドロップ先画像の1つ前に追加

              // ドロップした画像のstyleを元に戻す
              member.style.position = "relative";
              member.style.top = "0px";
              member.style.left = "0px";
              member.style.zIndex = 1;
            };
          });
        };
      };
      return ret;
    },

    mSfChangeBurst() { // ニケ画像のバースト表示変更
      this.sfDispBurst = !this.sfDispBurst;
