          if(!_this.qfFil_M_ABN && nikke.manufacturer == 'アブノーマル') disp = 'none';
        }

        if(_this.qfFil_M_ELY || _this.qfFil_M_MIS || _this.qfFil_M_TET || _this.qfFil_M_PIL || _this.qfFil_M_ABN) {
          if(!_this.qfFil_M_ELY && nikke.manufacturer == 'エリシオン') disp = 'none';
          if(!_this.qfFil_M_MIS && nikke.manufacturer == 'ミシリス') disp = 'none';
          if(!_this.qfFil_M_TET && nikke.manufacturer == 'テトラ') disp = 'none';
          if(!_this.qfFil_M_PIL && nikke.manufacturer == 'ピルグリム') disp = 'none';
          if(!_this.qfFil_M_ABN && nikke.manufacturer == 'アブノーマル') disp = 'none';
        }

        if(_this.qfFil_S_OH_BUS20 || _this.qfFil_S_OH_BUSXX) {
          let reg = /(バースト：20秒)/;
          if(!_this.qfFil_S_OH_BUS20 && reg.test(nikke.burst)) disp = 'none';
          if(!_this.qfFil_S_OH_BUSXX && !reg.test(nikke.burst)) disp = 'none';
        }

        if((_this.qfFil_S_UP_ATK
            || _this.qfFil_S_UP_CRIDMG
            || _this.qfFil_S_UP_CRIPER
