    mCalc(){ // パック一覧の「自分にとっての価値」の金額を計算
      var _this = this; // forEach内のthisは配列を指してしまうのでthisを別名にしておく
      
      this.$cookies.set('pJewel', this.pJewel, 0);
      this.$cookies.set('pTicketLuxury', this.pTicketLuxury, 0);
      this.$cookies.set('pTicketRegular', this.pTicketRegular, 0);
      this.$cookies.set('pMoldCompany', this.pMoldCompany, 0);
      this.$cookies.set('pMoldHigh', this.pMoldHigh, 0);
      this.$cookies.set('pMoldMiddle', this.pMoldMiddle, 0);
      this.$cookies.set('pNikeSelect', this.pNikeSelect, 0);
      this.$cookies.set('pCostume', this.pCostume, 0);
      this.$cookies.set('pCaseCredit', this.pCaseCredit, 0);
      this.$cookies.set('pCaseCore', this.pCaseCore, 0);
      this.$cookies.set('pCaseBattle', this.pCaseBattle, 0);
      this.$cookies.set('pManualSkill1', this.pManualSkill1, 0);
      this.$cookies.set('pManualSkill2', this.pManualSkill2, 0);
      this.$cookies.set('pManualSkill3', this.pManualSkill3, 0);
      this.$cookies.set('pManualBurst1', this.pManualBurst1, 0);
      this.$cookies.set('pManualBurst2', this.pManualBurst2, 0);
      this.$cookies.set('pManualBurst3', this.pManualBurst3, 0);
      this.$cookies.set('pManualAttribute', this.pManualAttribute, 0);
      this.$cookies.set('pCompanyArms', this.pCompanyArms, 0);
      this.$cookies.set('pCustomModule', this.pCustomModule, 0);
      this.$cookies.set('pModuleHigh', this.pModuleHigh, 0);
      this.$cookies.set('pModule', this.pModule, 0);
      this.$cookies.set('pConsoleClass', this.pConsoleClass, 0);
      this.$cookies.set('pConsoleCompany', this.pConsoleCompany, 0);
      this.$cookies.set('pEquipmentT9', this.pEquipmentT9, 0);
      this.$cookies.set('pEquipmentT8', this.pEquipmentT8, 0);
      this.$cookies.set('pEquipmentT7', this.pEquipmentT7, 0);
      this.$cookies.set('pFavorability500', this.pFavorability500, 0);
      this.$cookies.set('pFavorability100', this.pFavorability100, 0);
      this.$cookies.set('pFavorability50', this.pFavorability50, 0);

      this.packs.forEach(function(pack){
        pack.myPrice = _this.pJewel * pack.items.Jewel // ジュエル
                    + _this.pTicketLuxury * pack.items.TicketLuxury // 高級募集チケット
                    + _this.pTicketRegular * pack.items.TicketRegular // 通常募集チケット
                    + _this.pMoldCompany * pack.items.MoldCompany // 企業モールド
                    + _this.pMoldHigh * pack.items.MoldHigh // ハイクオリティモールド
                    + _this.pMoldMiddle * pack.items.MoldMiddle // ミドルクオリティモールド
                    + _this.pNikeSelect * pack.items.NikeSelect // ニケ選択ボックス
                    + _this.pCostume * pack.items.Costume // コスチューム
                    + _this.pCaseCredit * pack.items.CaseCredit // クレジットケース
                    + _this.pCaseCore * pack.items.CaseCore // コアダストケース
                    + _this.pCaseBattle * pack.items.CaseBattle // バトルデータケース
                    + _this.pCaseCredit * pack.items.Credit * 1000 / _this.coRewardCredit // クレジット1k
                    + _this.pCaseCore * pack.items.Core / _this.coRewardCore // コアダスト
                    + _this.pManualSkill1 * pack.items.ManualSkill1 // スキルマニュアルⅠ
                    + _this.pManualSkill2 * pack.items.ManualSkill2 // スキルマニュアルⅡ
                    + _this.pManualSkill3 * pack.items.ManualSkill3 // スキルマニュアルⅢ
                    + _this.pManualBurst1 * pack.items.ManualBurst1 // バーストマニュアルⅠ
                    + _this.pManualBurst2 * pack.items.ManualBurst2 // バーストマニュアルⅡ
                    + _this.pManualBurst3 * pack.items.ManualBurst3 // バーストマニュアルⅢ
                    + _this.pManualAttribute * pack.items.ManualAttribute // 属性コードマニュアル
                    + _this.pCompanyArms * pack.items.CompanyArms // カンパニーアームズ
                    + _this.pCustomModule * pack.items.CustomModule // カスタムモジュール
                    + _this.pModuleHigh * pack.items.ModuleHigh // モジュールハイブースト
                    + _this.pModule * pack.items.Module // モジュールブースト
                    + _this.pConsoleClass * pack.items.ConsoleClass // クラスコンソール
                    + _this.pConsoleCompany * pack.items.ConsoleCompany // 企業コンソール
                    + _this.pEquipmentT9 * pack.items.EquipmentT9 // T9企業装備選択ボックス
                    + _this.pEquipmentT8 * pack.items.EquipmentT8 // T8装備選択ボックス
                    + _this.pEquipmentT7 * pack.items.EquipmentT7 // T7装備選択ボックス
                    + _this.pFavorability500 * pack.items.Favorability500 // 企業VIPチケット
                    + _this.pFavorability100 * pack.items.Favorability100 // 指揮官利用し放題券
                    + _this.pFavorability50 * pack.items.Favorability50 // 指揮官デート券
                    + Math.max(_this.pCaseCredit * 3
                              , _this.pCaseCore
                              , _this.pCaseBattle
                              ) * pack.items.SupplyCG1 // 中央政府補給物資Ⅰ
                              
                    + Math.max(_this.pManualSkill1 * 9
                              , _this.pManualSkill2 * 7
                              , _this.pManualSkill3 * 3
                              ) * pack.items.ManualSkillSelect // スキルマニュアル選択ボックス

                    + Math.max(_this.pManualBurst1 * 9
                              , _this.pManualBurst2 * 7
                              , _this.pManualBurst3 * 3
                              ) * pack.items.ManualBurstSelect // バーストマニュアル選択ボックス

                    + Math.max(_this.pCaseCredit * 30
                              , _this.pCaseCore * 10
                              , _this.pCaseBattle * 10
                              , _this.pManualSkill1 * 28
                              , _this.pManualSkill2 * 20
                              , _this.pManualSkill3 * 8
                              , _this.pManualBurst1 * 14
                              , _this.pManualBurst2 * 10
                              , _this.pManualBurst3 * 4
                              ) * pack.items.Supply30Day // 30-DAY成長補給箱

                    + Math.max(_this.pCaseCredit * 25 + _this.pCaseCore * 25 + _this.pCaseBattle * 25
                              , _this.pCaseCredit * 150
                              , _this.pCaseCore * 50
                              , _this.pCaseBattle * 50
                              ) * pack.items.CustomPack1 // カスタムパックⅠ(選択枠1)
                    + Math.max(_this.pManualSkill1 * 200
                              , _this.pManualSkill2 * 140
                              , _this.pManualBurst1 * 100
                              , _this.pManualBurst2 * 70
                              ) * pack.items.CustomPack1 // カスタムパックⅠ(選択枠2)
                    
                    + Math.max(_this.pCompanyArms * 200
                              , _this.pCaseCredit * 50 + _this.pCaseCore * 50 + _this.pCaseBattle * 50
                              , _this.pCaseCredit * 300
                              , _this.pCaseCore * 100
                              , _this.pCaseBattle * 100
                              ) * pack.items.CustomPack2 // カスタムパックⅡ(選択枠1)
                    + Math.max(_this.pCustomModule * 2
                              , _this.pManualSkill1 * 250
                              , _this.pManualSkill2 * 180
                              , _this.pManualBurst1 * 125
                              , _this.pManualBurst2 * 90
                              ) * pack.items.CustomPack2 // カスタムパックⅡ(選択枠2)
                    + Math.max(_this.pManualSkill1 * 360
                              , _this.pManualSkill2 * 250
                              , _this.pManualSkill3 * 100
                              , _this.pManualBurst1 * 180
                              , _this.pManualBurst2 * 125
                              , _this.pManualBurst3 * 50
                              ) * pack.items.CustomPack2 // カスタムパックⅡ(選択枠3)
                    
                    + Math.max(_this.pCompanyArms * 200
                              , _this.pCaseCredit * 50 + _this.pCaseCore * 50 + _this.pCaseBattle * 50
                              , _this.pCaseCredit * 300
                              , _this.pCaseCore * 100
                              , _this.pCaseBattle * 100
                              ) * pack.items.CustomPack3 // カスタムパックⅢ(選択枠1)
                    + Math.max(_this.pCustomModule * 3
                              , _this.pManualSkill1 * 300
                              , _this.pManualSkill2 * 220
                              , _this.pManualBurst1 * 150
                              , _this.pManualBurst2 * 110
                              ) * pack.items.CustomPack3 // カスタムパックⅢ(選択枠2)
                    + Math.max(_this.pManualSkill1 * 360
                              , _this.pManualSkill2 * 250
                              , _this.pManualSkill3 * 100
                              , _this.pManualBurst1 * 180
                              , _this.pManualBurst2 * 125
                              , _this.pManualBurst3 * 50
                              ) * pack.items.CustomPack3 // カスタムパックⅢ(選択枠3)
                    + Math.max(_this.pTicketRegular * 30
                              , _this.pEquipmentT9 * 1
                              ) * pack.items.CustomPack3 // カスタムパックⅢ(選択枠3)
                    ;

        pack.degree = pack.myPrice / pack.price * 100;  // お得度
      })
    },

    mReset(){ // 課金シミュ絞り込みリセット
      var _this = this;
      this.divisions.forEach(function(division){
        division.check = false;
      });
      this.contents.forEach(function(content){
        content.check = false;
      });
    },

    mSortBy(key1, key2) {
      this.sort_key1 === key1 ? (this.sort_asc = !this.sort_asc) : (this.sort_asc = true)
      this.sort_key1 = key1
      this.sort_key2 = key2
    },

    mAddClass(key) {
      return {
        asc: this.sort_key1 === key && this.sort_asc,
        desc: this.sort_key1 === key && !this.sort_asc
      }
    }
  },

  computed: {
    packs_sort() { // 課金パック一覧の絞り込みとソート
      var _this = this; // forEach内のthisは配列を指してしまうのでthisを別名にしておく

      // ソート
      if(this.sort_key1 != "") {
        let set = 1;
        this.sort_asc ? (set = 1) : (set = -1)
        this.packs.sort((a, b) => {
          //if (a[this.sort_key2] > b[this.sort_key2]) return -1;
          //if (a[this.sort_key2] < b[this.sort_key2]) return 1;
          if (a[this.sort_key1] < b[this.sort_key1]) return -1 * set;
          if (a[this.sort_key1] > b[this.sort_key1]) return 1 * set;
          return 0;
        })
      }

      // 絞り込み
      var data = _this.packs;

      //表示対象区分
      let flgDivAll = true;
      _this.divisions.forEach(function(division){
        if(division.check){
          flgDivAll = false;
        }
      })
      if(!flgDivAll) {
        _this.divisions.forEach(function(division){
          if(!division.check){
            data = data.filter(function (row) {
              return row['division'].indexOf(division.name) == -1;
            });
          }
        })
      }

      //入力した条件で絞り込み
      let flgConAll = true;
      _this.contents.forEach(function(content){
        if(content.check){
          flgConAll = false;
        }
      })
      if(!flgConAll) {
        data = data.filter(function (row) {
          let flgFing = false;
          _this.contents.forEach(function(content){
            if(content.check){
              if(row['content'].indexOf(content.name) >= 0) {
                flgFing = true
              }
            }
          });
          return flgFing;
        });
      }

      //ハイライト設定
      let reg1 = new RegExp("<mark style='background-color: #f7f463;'>",'g');
      let reg2 = new RegExp("<mark style='background-color: #f18960;'>",'g');
      let reg3 = new RegExp("<mark style='background-color: #86ff40;'>",'g');
      let reg4 = new RegExp("<mark style='background-color: #659bff;'>",'g');
      let reg5 = new RegExp("<mark style='background-color: #f742c9;'>",'g');
      let regE = new RegExp("</mark>",'g');
      let num = 1;
      _this.packs.forEach(function(pack){
        pack.content = pack.content.replace(reg1, "");
        pack.content = pack.content.replace(reg2, "");
        pack.content = pack.content.replace(reg3, "");
        pack.content = pack.content.replace(reg4, "");
        pack.content = pack.content.replace(reg5, "");
        pack.content = pack.content.replace(regE, "");
      })
      _this.contents.forEach(function(content){
        if (content.check) {
          let reg3 = new RegExp(content.name,'g');
          _this.packs.forEach(function(pack){
            
            switch(num){
              case 1:
                pack.content = pack.content.replace(reg3, "<mark style='background-color: #f7f463;'>" + content.name + "</mark>");
                break;
              case 2:
                pack.content = pack.content.replace(reg3, "<mark style='background-color: #f18960;'>" + content.name + "</mark>");
                break;
              case 3:
                pack.content = pack.content.replace(reg3, "<mark style='background-color: #86ff40;'>" + content.name + "</mark>");
                break;
              case 4:
                pack.content = pack.content.replace(reg3, "<mark style='background-color: #659bff;'>" + content.name + "</mark>");
                break;
              default:
                pack.content = pack.content.replace(reg3, "<mark style='background-color: #f742c9;'>" + content.name + "</mark>");
            }
          })
          if(++num > 5){
            num = 1;
          }
        }
      })

      return data
    }
  },

  destroyed: function () { // PT作成支援関連
    window.removeEventListener('dragstart', function(e){}, false)
    window.removeEventListener('drag', function(e){}, false)
    window.removeEventListener('dragover', function(e){}, false)
    window.removeEventListener('drop', function(e){}, false)
    //clearInterval(this.updateMessageTimer)
  }

}
</script>


